import React, { Component, PropTypes } from 'react';
import KlassName from 'teapan/klassName';
import { SIZES, SKINS } from '../../constant/props';

import Ripple from '../../enhance/Ripple/index';

const noop = () => {};

class Button extends Component {
	constructor(props) {
		super(props);
		this.state = {};
		this.handleClick = this.handleClick.bind(this);
		this.handleMouseUp = this.handleMouseUp.bind(this);
		this.handleMouseLeave = this.handleMouseLeave.bind(this);
	}

	handleClick(...args) {
		this.props.onClick(...args);
	}

	handleMouseUp(event) {
		this.refs.button.blur();
		if (this.props.onMouseUp) this.props.onMouseUp(event);
	}

	handleMouseLeave(event) {
		this.refs.button.blur();
		if (this.props.onMouseLeave) this.props.onMouseLeave(event);
	}

	render() {
		const props = this.props;
		let {
			activated,
			loading,
			disabled,
			href,
			size,
			kind,
			skin,
			shape,
			type,
			className,
			children,
			...others
		} = props;

		const klassName = KlassName('button',{
			['button--' + size]:size,
			['button--' + kind]:kind,
			['button--' + skin]:skin,
			['button--' + shape]:shape,
			['activated']:('activated' in props && activated !== false),
			['loading']:('loading' in props && loading !== false),
			['disabled']:('disabled' in props && disabled !== false),
		},className);

		if (href) {
			return (
				<a
					{...others}
					className={klassName}
					onClick={this.handleClick}
					onMouseUp={this.onMouseUp}
					onMouseLeave={this.onMouseLeave}
					href={href}
					ref='button'
					data-teadot='button'
				>
					{children}
				</a>
			);
		} else {
			return (
				<button
					{...others}
					className={klassName}
					onClick={this.handleClick}
					onMouseUp={this.onMouseUp}
					onMouseLeave={this.onMouseLeave}
					type={type || 'button'}
					ref='button'
					data-teadot='button'
				>
					{children}
				</button>
			);
		}
	}

};

Button.propTypes = {
	activated: PropTypes.bool,
	loading: PropTypes.bool,
	disabled: PropTypes.bool,

	size: PropTypes.oneOf(SIZES),
	skin: PropTypes.oneOf(SKINS),
	kind: PropTypes.oneOf(['hollow','link']),
	type: PropTypes.oneOf(['submit', 'button', 'reset']),
	shape: PropTypes.oneOf(['block','circle']),

	href: PropTypes.string,
	className: PropTypes.string,

	onClick: PropTypes.func,
	onMouseUp: PropTypes.func,
	onMouseLeave: PropTypes.func,
};

Button.defaultProps = {
	loading: false,
	disabled: false,
	onClick: noop
};

export default Ripple({centered: false})(Button);